@use "../../../style/mixin" as *;

.aboutme {
  @include flex;
  @include alignItems;
  justify-content: center;
  width: 100%;
  height: 100vh;
  // color: black;
  // background-color: white;
  color: white;
  background-color: black;
  .text-box {
    width: 100%;
    height: 100%;
    padding: 150px;
    .title {
      position: relative;
      width: 17vw;
      height: 17vw;
      min-height: 40vmin;
      min-width: 40vmin;
      @include flex(column);
      @include alignItems;
      justify-content: center;
      font-family: "Inter";
      font-style: normal;
      font-weight: 700;
      font-size: 6vmin;
      color: white;
      // @include bgOption('../../../style/image/border-background.png');
      &:after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        z-index: 0;
        @include bgOption("../../../style/image/star.gif");
      }
      .title-box {
        @include flex(column);
        align-items: center;
        justify-content: center;
        padding: 50px;
        width: 85%;
        height: 85%;
        text-align: left;
        background-color: black;
        z-index: 2;
        p {
          width: 100%;
          text-align: left;
        }
      }
    }
    .text {
      margin: 50px 0;
      font-family: "NanumBarunpen";
      font-style: normal;
      font-weight: 400;
      font-size: 2.2vmin;
      word-break: keep-all;
    }
  }
  .right-bg {
    width: 590px;
    height: 100%;
    @include bgOption("../../../style/image/right-background.png");
  }
}
