@use "../../../style/mixin" as *;

.project {
  animation: ani-fadin 0.5s;
  .title {
    position: fixed;
    right: 150px;
    top: 150px;
    font-family: "Inter";
    font-style: normal;
    font-weight: 700;
    font-size: 6vmin;
    color: white;
  }
  .contents {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 150px;
  }
  .column-1,
  .column-2,
  .column-3,
  .column-4,
  .column-5 {
    position: relative;
    height: 100vh;
    opacity: 0;
    // transition: all 0.3s;
    z-index: 0;
    &.active {
      opacity: 1;
      z-index: 5;
    }
    .sub-title {
      position: fixed;
      top: 150px;
      left: 150px;
      font-family: "Inter";
      font-style: normal;
      font-weight: 700;
      font-size: 3vmin;
      color: white;
      transform: rotate(90deg);
      transform-origin: top left;
      text-align-last: left;
    }
  }
}
